const COS=require("./cos-wx-sdk-v5.js")


//
function uploadFileToTencentClound(filename,filePath,config){
	
	
	const Bucket=config.Bucket;// BucketName-APPID
	const Region=config.Region;//
	// COS SDK 
	// SECRETID  SECRETKEY https://console.cloud.tencent.com/cam/capi 
	const cos = new COS({
	  SecretId: config.SecretId,
	  SecretKey:config.SecretKey,
	});
	//
	cos.putBucket({
	  Bucket:config.Bucket,
	  Region:config.Region,
	}, function (err, data) {
	  console.log(err || data);
	});
	
	
	
	
	
	return new Promise((resolve,reject)=>{
		cos.postObject({
		    Bucket: Bucket,
		    Region: Region,
		    Key: 'wxFile/' + filename,
		    FilePath: filePath,
		    onProgress: function (info) {
		        console.log("[cos.postObject-seccess]",JSON.stringify(info));
		    }      
		}, 
		function (err, data) {	
			console.log(data.Location)
			resolve(data.Location)
		})
	})
}
export default {
	uploadFileToTencentClound
}
